<template>
  <div class="container">
    <vue-headful
      title="About | Rhys Matthew | Web Developer"
      description="Rhys Matthew is based in Nottingham, UK. He has a passion for building digital interfaces and creating interactive websites. Rhys loves clean modular code and modern JavaScript frameworks like Vue or React."
      keywords="About Rhys Matthew, About, UI developer, UX Design, Web Design, Front-End Development, JavaScript dev, Freelance, web engineer, CSS, PHP, Wordpress developer"
      :head="{
        'meta[property=\'og:url\']': {content: 'https://rhys.digital/about/'},
        'meta[name=\'twitter:site\']': {content: 'https://rhys.digital/about/'},
        'link[rel=\'canonical\']': {href: 'https://rhys.digital/about/'}
      }"
    />

    <section class="section pb-0">
      <div class="mw--650">
        <div class="section__header">
          <h1 class="h2 reveal"><div class="reveal-inner">About</div></h1>
        </div>
        <div class="section__block">
          <div class="reveal reveal--delay-1">
            <div class="reveal-inner">
              <prismic-rich-text :field="fields.aboutIntro"/>
            </div>
          </div>
        </div>
      </div>
    </section>

    <section class="section pb-0">
      <div class="mw--650">
        <div class="section__header">
          <h2 class="h2 reveal reveal--delay-2"><div class="reveal-inner">Experience with</div></h2>
        </div>
        <div class="section__block">
          <div class="reveal reveal--delay-3">
            <div class="reveal-inner">
              <ul class="list list--flex">
                <li v-for="(aboutTool, index) in fields.aboutTools" :key="index">
                  {{ aboutTool.about_tool }}
                </li>
              </ul>
            </div>
          </div>
        </div>
      </div>
    </section>

    <section class="section">
      <div class="mw--650">
        <div class="section__header">
          <h2 class="h2 reveal"><div class="reveal-inner">What I can do for you</div></h2>
        </div>
        <div class="section__block">
          <div class="reveal">
            <div class="reveal-inner">
              <ul class="list">
                <li v-for="(aboutService, index) in fields.aboutServices" :key="index">
                  {{ aboutService.about_service }}
                </li>
              </ul>
            </div>
          </div>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
// Reveal
import reveal from '../components/reveal/_reveal.js'

export default {
  data: function () {
    return {
      fields: {
        aboutIntro: [],
        aboutTools: [],
        aboutServices: []
      }
    }
  },
  created () {
    const self = this

    // After app initialisation, set page's data
    //
    if (window.app.appInitialised === true) {
      self.fields.aboutIntro = window.about.fields.aboutIntro
      self.fields.aboutTools = window.about.fields.aboutTools
      self.fields.aboutServices = window.about.fields.aboutServices
    }

    if (window.app.appInitialised === false) {
      document.addEventListener('appInitialised', function () {
        self.fields.aboutIntro = window.about.fields.aboutIntro
        self.fields.aboutTools = window.about.fields.aboutTools
        self.fields.aboutServices = window.about.fields.aboutServices
      })
    }
  },
  mounted () {
    // eslint-disable-next-line
    setTimeout (() => {
      // Dispatch pageChange event
      //
      const pageChange = new Event('pageChange')
      document.dispatchEvent(pageChange)

      // Initialise reveal effect after content has had
      // enough time to render
      //
      reveal.init()
    }, 1500)
  }
}
</script>
